<template>
    <div class="arbitration">
        <div class="title">{{$localizeFilter('arbitration_title')}}</div>
        <div class="subtitle">{{$localizeFilter('arbitration_subtitle')}}</div>
        <LargeToggle :title="null" :variants="chainTypes" @toggle="toggle" class="toggle"/>
        <div class="chain-amount first-block">
          <AmountView class="chain-amount-first" :title="$localizeFilter('amount_income')" :amount="totalIncome"  :color="'type-green'"/>
          <AmountView v-if="selectedChainType==='chains_in_work'" :title="$localizeFilter('amount_test')" :amount="testBalance" :color="'type-gray'"/>   
        </div>
        <div v-if="selectedChainType==='chains_in_work'" class="chain-amount">
          <AmountView :title="$localizeFilter('chains_amount_deposited')" :amount="totalInWorkInChains" :color="'type-gold'"/>   
        </div>

        <div v-if="this.selectedChainType === 'all_chains'">
            <TransitionGroup name="list" tag="div" class="chains-container all-chains">
                <!-- <div v-for="(chain, idx) in chains" :key="idx"> -->
                    <ArbitrationChain v-for="chain in chains" :key="chain.chainId" :chain="chain" :type="selectedChainType" :coinsNames="coinsNames" @deposit="deposit"
                    :isNewChain="isNewChain(chain)"
                    />  
                <!-- </div> -->
            </TransitionGroup>
        </div>

        <div v-if="this.selectedChainType === 'chains_in_work'">
            <TransitionGroup name="list" tag="div" class="chains-container all-chains">
                <div v-for="chain in chains" :key="chain.chainId">
                    <ArbitrationChain :chain="chain" :type="selectedChainType" :coinsNames="coinsNames" @deposit="deposit"/>  
                </div>
            </TransitionGroup>
        </div>

        <ArbitrationDepositModal v-if="showDepositModal" :chain="currentChain" :testBalance="testBalance" @closeModal="closeModals"/>
        <ArbitrationVideoModal v-if="showVideoModal" @skipVideo="skipVideo"/>
        <ArbitrationConfirmationModal v-if="showAgreementModal" @closeModal="closeModals"/>
    </div>
</template>
    
<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
import LargeToggle from "@/components/LargeToggle.vue"
import ArbitrationChain from '../components/ArbitrationChain.vue'
import AmountView from '../components/AmountView.vue'
import ArbitrationDepositModal from '../components/newModals/ArbitrationDepositModal.vue'
import ArbitrationVideoModal from '../components/newModals/ArbitrationVideoModal.vue'
import ArbitrationConfirmationModal from '../components/newModals/ArbitrationConfirmationModal.vue'

export default defineComponent({
    components: {
        LargeToggle,
        ArbitrationChain,
        AmountView,
        ArbitrationDepositModal,
        ArbitrationVideoModal,
        ArbitrationConfirmationModal
    },
    setup() {
    },
    watch: { 
        chains(newVal, oldVal) {
            // console.log(newVal, oldVal)
            this.$nextTick(()=>{
                this.chainsDiff = this.getDiff(JSON.parse(JSON.stringify(newVal)), JSON.parse(JSON.stringify(oldVal)))
                // console.log(this.chainsDiff)
            })
        }
    },
    data: () => ({
        chainTypes: ['all_chains', 'chains_in_work'],
        selectedChainType: 'all_chains',
        showDepositModal: false,
        currentChain: null,
        showVideoModal: false,
        showAgreementModal: false,
        chains: [],
        chainsDiff: []
    }),
    computed: {
        arbitrationData() {
            return this.$store.getters.arbitrationData
        },
        arbitrationRisksConditionsAccepted() {
            return this.arbitrationData?.arbitrationRisksConditionsAccepted || false
        },
        totalIncome() {
            return this.arbitrationData?.totalIncome || 0
        },
        coinsNames() {
            return this.arbitrationData?.coinsNames || {}
        },
        testBalance() {
            return this.arbitrationData?.testBalance || 0
        },
        totalInWorkInChains() {
            return this.arbitrationData?.totalInWorkInChains || 0
        },
    },
    methods: {
        isNewChain(chain) {
            const res = this.chainsDiff.some(diffItem => diffItem.chainId === chain.chainId)
            // console.log(res)
            return res
        },
        getDiff(newVal, oldVal) {
            let diff = []
            // console.log(newVal, oldVal)
            if (oldVal.length === 0) { return []}
            newVal.forEach(newItem => { 
                // если в oldVal не встречается ни одного item с таким же chainId то пушим в diff
                const found = oldVal.some(oldItem => oldItem.chainId === newItem.chainId);
                if (!found) {
                    diff.push(newItem)
                }
            });
            return diff
        },
        skipVideo() {
            this.showVideoModal = false
            setTimeout(()=>{
                this.showAgreementModal = true
            })
        },
        deposit(chain) {
            this.currentChain = chain
            this.showDepositModal = true
        },
        closeModals() {
            this.showDepositModal = false
            this.showWithdrawModal = false
            this.currentChain = null
            this.showVideoModal = false
            this.showAgreementModal = false
            this.loadData()
        },
        toggle(index) {
            this.selectedChainType = this.chainTypes[index]
        },
        async loadData() {
            try {
                await this.$store.dispatch('getArbitrageChainsData')
            }
                catch (e) {
                this.$error(e)
            }  
        }
    },
    async mounted() {
    await this.loadData()

    if (this.arbitrationRisksConditionsAccepted === false) {
        this.showVideoModal = true
    }

    setInterval(async () => {
        await this.loadData()
        if (this.selectedChainType === 'all_chains') {
            this.chains = this.arbitrationData?.allChains || []
        } else if (this.selectedChainType === 'chains_in_work') {
            this.chains = this.arbitrationData?.chainsInWork || []
        } else {
            this.chains = []
        }
    }, 3000)
    }  
})
</script>

